body {
  margin: 0;
}
.contianer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.header {
  padding: 0px 15px;
  border: 1px solid tomato;
  border-radius: 2rem;
  color: white;
  background-color: tomato;
}

.input {
  width: 300px;

  display: inline-block;
  justify-content: center;
  font-size: 22px;
  background-color: rgb(239, 186, 177);
  border-radius: 2rem;
  color: rgb(255, 255, 255);
  border: none;
  margin-top: 30px;
  padding: 5px 10px;
}
.input::placeholder {
  color: white;
}
.input:focus {
  outline: none;
}

button {
  display: inline-block;
  justify-content: center;
  align-items: center;
  border: none;
  color: BLACK;
  background-color: rgba(255, 255, 255, 0.18);
  border-radius: 30%;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.listul {
  background-color: tomato;
  padding: 10px 20px;
  align-items: center;
  border-radius: 20px;
}

.listli {
  background-color: tomato;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  margin-bottom: 3px;
  width: 300px;
  color: white;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
